.row{
    margin-left: 20px;
    color: #fff;
}

.posters{
    display: flex;
    padding: 20px;
    overflow-x: scroll;
    overflow-y: hidden;
}

.posters::-webkit-scrollbar{
    display: none;
}

.poster{
    cursor: pointer;
    max-height: 250px;
    margin-right: 10px;
    position: relative;
    z-index: 0;
}

.smallPoster{
    position: relative;
    cursor: pointer;
    max-height: 150px;
    margin-right: 10px;
    z-index: 0;
}

.poster:hover{
    transform: scale(1.1);
}

.smallPoster:hover{
    transform: scale(1.1);

}
.movieName{
   position: sticky;
   z-index: 1;
   font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  visibility: hidden;
}

.posters :hover .movieName{
    visibility: visible;
} 

