.navbar{
    position: fixed;
    z-index: 2;
    top:0;
    width: 100%;
    height: 30px;
    padding: 20px;
    display: flex;
    justify-content: space-between;
    background-color: #111;
}



.logo{
    position: fixed;
    left: 20px;
    width: 80px;
    color: red;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.avatar{
    position: fixed;
    right: 20px;
    width: 30px;
}